import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';
import {md5} from 'lib/md5';
import { TestService } from '../test/rest-service';

@Component({
  selector: 'fd-template-driven',
  templateUrl: './template-driven.component.html',
  styleUrls: ['./template-driven.component.css']
})

export class TemplateDrivenComponent{
    constructor(private _testService: TestService) { }
    confirmarPassword=undefined;//Variable utilizada para realizar la comparación entre las contraseñas.
    
    user= {
        usuario:undefined,
        password:undefined,
        nombre:undefined,
        apellidos:undefined,
        email:undefined,
        edad:undefined,
        genero:undefined,
        direccion:''
    };//Variable que guardará los datos por registrar.
    
    dir={
        calle:undefined,
        colonia:undefined,
        cp:undefined,
        delegacion:undefined
    };//Variable para guardar la dirección por campos separados.
    genero=[
        'Masculino',
        'Femenino'
    ]//Opciones para mostrar en los radio button.
    
    //Función para enviar la información del usuario.
    //PARAMETROS form contiene la información ingresada en el formulario.
    onSubmit(form: NgForm){
        
        //Variables utilizada para la encriptación y desencriptación de la contraseña.
        //var key = CryptoJS.enc.Base64.parse("#base64Key#");
        //var iv  = CryptoJS.enc.Base64.parse("#base64IV#");
        
        //Variable que guardará la contraseña encriptada.
        let e = md5(this.user.password);
        //var encrypted = CryptoJS.AES.encrypt(this.user.password, key, {iv: iv});
    
        //Se convierte la variable encriptada en una cadena para poder enviarla.
        //var new_pass=encrypted.toString();
        //Desencripción
        //var decrypted = CryptoJS.AES.decrypt(new_pass, key, {iv: iv});
        //var c = decrypted.toString(CryptoJS.enc.Utf8);
        
        //Ciclo para concatenar las partes de la dirección.
        for (var key in this.dir){
            if(this.dir[key]!=undefined||this.dir[key]!='')
                this.user.direccion+= this.dir[key]+' ';
        }
    }
    
    test(){
        this._testService.getInfo()
        .$observable
        .subscribe((data:any) => console.log(data.obj));
    }
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    

}



