//Servicio REST para consumir WS
import {Injectable} from '@angular/core';
import {RestClient} from '../shared/rest-client';
import {ResourceParams, ResourceAction, ResourceMethod} from 'ng2-resource-rest';
import {RequestMethod} from '@angular/http';

@Injectable()
@ResourceParams({
  url: 'catalogos/getMenu/generales/ '
})
export class TestService extends RestClient{
  
@ResourceAction({
    method: RequestMethod.Get,
})
    getInfo: ResourceMethod<any, any>;

}


